\n  @if $legacy_support_for_ie {\n    *display: inline;\n    *zoom: 1;\n  }\n}\n\n// 1. Prevents modern browsers from displaying 'audio' without controls\n// 2. Remove excess height in iOS5 devices\n\naudio:not([controls]) {\n  display: none; // 1\n  height: 0; // 2\n}\n\n//\n// Address `[hidden]` styling not present in IE 8/9.\n// Hide the `template` element in IE, Safari, and Firefox < 22.\n//\n\n[hidden],\ntemplate {\n  display: none;\n}\n\n// =============================================================================\n// Base\n// =============================================================================\n\n// 1. Corrects text resizing oddly in IE6/7 when body font-size is set using em units\n//    http://clagnut.com/blog/348/#c790\n// 2. Prevents iOS text size adjust after orientation change, without disabling user zoom\n//    www.456bereastreet.com/archive/201012/controlling_text_size_in_safari_for_ios_without_disabling_user_zoom/\n\nhtml {\n  @if $legacy_support_for_ie {\n    font-size: 100%; // 1\n  }\n  background: $background;\n  color: $color;\n  -webkit-text-size-adjust: 100%; // 2\n  -ms-text-size-adjust: 100%; // 2\n}\n\n// Addresses font-family inconsistency between 'textarea' and other form elements.\n\nhtml,\nbutton,\ninput,\nselect,\ntextarea {\n  font-family: $normalized_font_family;\n}\n\n// Addresses margins handled incorrectly in IE6/7\n\nbody {\n  margin: 0;\n}\n\n// =============================================================================\n// Links\n// =============================================================================\n\n// 1. Remove the gray background color from active links in IE 10.\n// 2. Addresses outline displayed oddly in Chrome\n// 3. Improves readability when focused and also mouse hovered in all browsers\n//    people.opera.com/patrickl/experiments/keyboard/test\n\na {\n  // 1\n\n  background: transparent;\n\n  // 2\n\n  &:focus {\n    outline: thin dotted;\n  }\n\n  // 3\n\n  &:hover,\n  &:active {\n    outline: 0;\n  }\n}\n\n// =============================================================================\n// Typography\n// =============================================================================\n\n// Addresses font sizes and margins set differently in IE6/7\n// Addresses font sizes within 'section' and 'article' in FF4+, Chrome, S5\n\n@if $normalize_headings == true {\n  h1 {\n    font-size: $h1_font_size;\n    margin: $h1_margin;\n  }\n\n  h2 {\n    font-size: $h2_font_size;\n    margin: $h2_margin;\n  }\n\n  h3 {\n    font-size: $h3_font_size;\n    margin: $h3_margin;\n  }\n\n  h4 {\n    font-size: $h4_font_size;\n    margin: $h4_margin;\n  }\n\n  h5 {\n    font-size: $h5_font_size;\n    margin: $h5_margin;\n  }\n\n  h6 {\n    font-size: $h6_font_size;\n    margin: $h6_margin;\n  }\n}\n\n// Addresses styling not present in IE 8/9, S5, Chrome\n\nabbr[title] {\n  border-bottom: 1px dotted;\n}\n\n// Addresses style set to 'bolder' in FF3+, S4/5, Chrome\n\nb,\nstrong {\n  font-weight: bold;\n}\n\n@if $legacy_support_for_ie {\n  blockquote {\n    margin: 1em 40px;\n  }\n}\n\n// Addresses styling not present in S5, Chrome\n\ndfn {\n  font-style: italic;\n}\n\n// Addresses styling not present in IE6/7/8/9\n\nmark {\n  background: #ff0;\n  color: #000;\n}\n\n// Addresses margins set differently in IE6/7\n@if $legacy_support_for_ie {\n  p,\n  pre {\n    margin: 1em 0;\n  }\n}\n\n// Corrects font family set oddly in IE6, S4/5, Chrome\n// en.wikipedia.org/wiki/User:Davidgothberg/Test59\n\ncode,\nkbd,\npre,\nsamp {\n  font-family: monospace, serif;\n  @if $legacy_support_for_ie {\n    _font-family: 'courier new', monospace;\n  }\n  font-size: 1em;\n}\n\n// Improves readability of pre-formatted text in all browsers\n\npre {\n  white-space: pre;\n  white-space: pre-wrap;\n  word-wrap: break-word;\n}\n\n// Set consistent quote types.\n\nq {\n  quotes: '\\201C''\\201D''\\2018''\\2019';\n}\n\n// 1. Addresses CSS quotes not supported in IE6/7\n// 2. Addresses quote property not supp